import React, { useState } from 'react'
import { Input, InputNumber, Form, Modal, message, Button, Row, Col, Card, Popconfirm } from 'antd';
import { PlusOutlined, DeleteOutlined } from '@ant-design/icons';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import { DataService } from "services/accounts.service";

const Accounts = ({systemData}) => {
  const [form] = Form.useForm();
  const [visible, setVisible] = useState(false);
  const [confirmLoading, setConfirmLoading] = useState(false);
  const [editingKey, setEditingKey] = useState('');
  const { accounts, accountsLoading } = systemData;
  const cardDropdown = () => (
    <a href="/#" className="text-gray font-size-sm" onClick={e => {e.preventDefault(); showModal()}}>
      <PlusOutlined className="text-gray font-size-sm"/> Create New
    </a>
  )
  const edit = record => {
    form.setFieldsValue({ Oldbank:record.bank,Oldaccount:record.account });
    setEditingKey(record._id);
  };

  const cancel = () => {
    setEditingKey('');
    form.setFieldsValue({Oldbank:null,Oldaccount:null});
  };

  const save = async () => {
    try {
      const row = await form.validateFields();
      let bank = row.Oldbank.slice(0,1) === ' ' ? row.Oldbank.slice(1) : row.Oldbank;
      let account = row.Oldaccount;
      if (bank==='' || account===0) return;
      let accountData = {bank, account}
      DataService.update(editingKey,accountData)
      setEditingKey('');
      form.setFieldsValue({Oldbank:null,Oldaccount:null});
      message.success(`Account updated`);
    } catch (errInfo) {}
  };

  const remove = () => {
    DataService.remove(editingKey)
    message.success(`Account deleted`);
  };

  const showModal = () => {
    setVisible(true);
  }
  const handleOk = async e => {
    try {
      const values = await form.validateFields();
      let bank = values.bank.slice(0,1) === ' ' ? values.bank.slice(1) : values.bank;
      let account = values.account;
      if (bank==='' || account===0) return;
      setConfirmLoading(true);
      let accountData = { bank, account }
      DataService.create(accountData)
      setVisible(false);
      setConfirmLoading(false);
      form.setFieldsValue({bank:null,account:null});
    } catch (errInfo) {}
    
  }
  const handleCancel = () => {
    setVisible(false);
    form.setFieldsValue({bank:null,account:null});
  }
	return (
		<Card title="Accounts" extra={cardDropdown()}>
            <div >
              {
                !accountsLoading && accounts.docs.map((elm, i) => (
                  <div key={i} className={`d-flex align-items-center justify-content-between mb-4 cursor-pointer`} onClick={() => {edit(elm.data())}}>
                    <AvatarStatus id={i} name={elm.data().account} subTitle={elm.data().bank} size='small'/>
                  </div>
                ))
              }
            </div>
            <Form form={form} component={false} layout="vertical">
              <Modal
                title="New Account"
                visible={visible}
                onOk={handleOk}
                confirmLoading={confirmLoading}
                onCancel={handleCancel}
              >
                <Row gutter={16}>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item name="account" label="Account Number" rules={[
                      {
                        required: true,
                        message: `Account Number is required.`,
                      },
                    ]}>
                      <InputNumber
                        className="w-100"
                        parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item name="bank" label="Bank Name" rules={[
                      {
                        required: true,
                        message: `Bank name is required.`,
                      },
                    ]}>
                      <Input/>
                    </Form.Item>
                  </Col>
                </Row>
              </Modal> 
              <Modal
                title="Update Account"
                visible={editingKey}
                onOk={save}
                onCancel={cancel}
                footer={[
                  <Popconfirm
                    title="Are you sure delete this?"
                    onConfirm={remove}
                    okText="Yes"
                    cancelText="No"
                  >
                    <Button key="delete" className="float-left">
                      <DeleteOutlined/> Delete
                    </Button>
                  </Popconfirm>,
                  <Button key="back" onClick={cancel}>
                  Cancel
                </Button>,
                  <Button key="submit" type="primary" loading={confirmLoading} onClick={save}>
                    OK
                  </Button>,
                ]}
              >
                <Row gutter={16}>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item name="Oldaccount" label="Account Number" rules={[
                      {
                        required: true,
                        message: `Account Number is required.`,
                      },
                    ]}>
                      <InputNumber
                        className="w-100"
                        parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item name="Oldbank" label="Bank Name" rules={[
                      {
                        required: true,
                        message: `Bank name is required.`,
                      },
                    ]}>
                      <Input/>
                    </Form.Item>
                  </Col>
                </Row>
              </Modal> 
            </Form> 
        </Card>
	)
}

export default Accounts